.form__button {
  width: 100%;
  height: 55px;
  color: rgba(210, 40, 86, 1);
  border-color: rgba(210, 40, 86, 1);
  @media (min-width: @tablet-width) {
    width: 95%;
    margin-right: auto;
  }
  @media (min-width: @desktop-width) {
    font-size: 21px;
  }
}

.form__button:first-of-type {
  margin-bottom: 12px;
  @media (min-width: @tablet-width) {
    margin-bottom: 24px;
  }
  @media (min-width: @desktop-width) {
    margin: 0;
  }
}
.form__button:disabled {
  color: rgba(40, 54, 69, 0.1);
  border-color: rgba(40, 54, 69, 0.1);
  
}